	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue";
	export default {
		data() {
			return {
				
			}
		},
		methods: {
			clickXiaoqu(){
            if (!this.hasLogin) {
                uni.showModal({
                    title: '温馨提示',
                    content: '您未登录，需要登录后才能继续',
                    /**
                     * 如果需要强制登录，不显示取消按钮
                     */
                    showCancel: !this.forcedLogin,
                    success: (res) => {
                        if (res.confirm) {
							/**
							 * 如果需要强制登录，使用reLaunch方式
							 */
                            if (this.forcedLogin) {
                                uni.reLaunch({
                                    url: '../login/login'
                                });
                            } else {
                                uni.navigateTo({
                                    url: '../login/login'
                                });
                            }
                        }
                    }
                });
            }				
				console.log("小区信息-请登录注册！")
			} ,			
			onClickLeft(){
				uni.navigateBack({
					delta: 1,
					animationType: 'pop-out',
					animationDuration: 200
				});				
				//console.log(555)
			},
			ClickBotton(){
				uni.navigateTo({
					url: '../login/login'
				});
			},
			Reg(){
				uni.navigateTo({
					url: '../reg/reg'
				});				
			}
		},
		components: {uniNavBar}
	}
